/*-----Fonts-----*/
@font-face {
	font-family: Raleway;
	src: url("./Raleway-VariableFont_wght.ttf");
}

.app__container {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      min-height: 100vh;
      background-image: url('./WaterMark.svg');
      z-index: 1;
}

.app__header {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
      align-items: center;
      height: 10vh;
      width: 100%;
      background-color: #001427;
      box-shadow: -1px 4px 20px rgba(0, 0, 0, 0.2);
      z-index: 2;
}

.app__body {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 5vh;
      height: auto;
      width: 88%;
      z-index: 2;
}


.app__header > h1 {
      font-family: Raleway;
      text-align: center;
      font-size: 2rem;
      letter-spacing: 2px;
      color: white;
      font-weight: bold;
      padding-left: 2vw;
}

.app__header > img {
      height: 70%;
      width: auto;
      padding-right: 2vw;
}

.header__span {
      border-bottom: 3px solid white;
      padding-bottom: 5px;
}

.body__form {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      height: auto;
      padding: 2rem;
      background-color: white;
      box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.2);
}

.body__form > h1, .body__table > h1 {
      font-size: 2.5rem;
      font-family: Raleway;
      text-align: center;
      letter-spacing: 1px;
      color: #00001D;
      margin-bottom: 3vh;
}

.body__form > form {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 90%;
      height: auto;
}

.form__container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      flex: 1;
      height: auto;
      width: 100%;
      margin-bottom: 10vh;
}

.form__containerSection {
      display: flex;
      flex-direction: column;
      flex: .4;
      height: auto;
}

.form__containerSection > *, .body__form > form > select, h2, textarea{
      padding: 1rem 1.5rem;
	font-size: 1rem;
	font-family: "Raleway", sans-serif;
	font-weight: 500;
	letter-spacing: 2px;
	border: 1px solid #dddddd;
	resize: none;
      width: 100%;
}

.form__containerSection > select, .form__containerSection > select > option {
      min-width: 110%;
      max-width: 120%;
}

.form__containerSection > h2, .body__form > form > h2 {
      font-size: 1.6rem;
      padding: 2rem 0 1rem 1rem;
      border: none;
}

.body__form > form > h2 {
      text-align: center;
}

.form__span {
      opacity: .5;
      font-size: 1.2rem;
      font-style: italic;
}

.body__form > form > textarea {
      min-height: 15vh;
      width: 96%;
}

.body__form > form > button {
      margin-top: 5vh;
      align-self: center;
      padding: 1rem;
      font-size: 1rem;
	font-family: "Raleway", sans-serif;
	font-weight: 500;
	letter-spacing: 2px;
      border: 1px solid #dddddd;
}

.body__table {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      height: auto;
      padding: 2rem;
      margin-top: 5vh;
      background-color: white;
      box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.2);
}

.body__table > table {
      width: 90%;
}

.body__table > table > tr:first-child > th {
      font-weight: bold;
}

.body__table > table > tr > th {
      padding: .6rem;
      font-size: 1rem;
	font-family: "Raleway", sans-serif;
	font-weight: 500;
	letter-spacing: 2px;
      border: 1px solid #dddddd;
}

.app__footer {
      margin-top: 5vh;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      height: 15vh;
      width: 100%;
      background-color: #001427;
}

.app__footer > h1 {
      font-family: Raleway;
      text-align: center;
      font-size: 2rem;
      letter-spacing: 2px;
      color: white;
      font-weight: bold;
}